.root {
    background-color: #000000;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
}
@media (max-width: 890px) {
   .main {
    margin: 0 19px;
    }
}

.header {
    margin: 0 0 40px 0;
    padding-bottom: 41px;
    max-width: 880px;
    border-bottom: 1px solid;
    border-bottom-color: rgba(84, 84, 84, 0.7);
    display: flex;
    justify-content: space-between;
}

@media (max-width: 890px) {
    .header {
        padding-bottom: 32px;
        margin-bottom: 42px;
        margin-top: 28px;
    }
}
.header__logo {
    width: 142px;
    height: 33px;
}

@media (max-width: 890px) {
    .header__logo {
        width: 104px;
        height: 24px;
        margin-left: 27px;
    }
}
.header__link {
    font-family: Inter, Arial, sans-serif;
    max-height: 22px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: white;
    text-decoration: none;
    
  }
  
  .header__link:hover {
    opacity: 0.6;
  }
.header__navbar {
    font-family: Inter, Arial, sans-serif;
    display: flex;
    flex-direction: row;
  }
.header__signout {
    background-color: black;
    font-family: Inter, Arial, sans-serif;
    width: 56px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #A9A9A9;
    text-decoration: none;
    margin-left: 24px;
  }
  
  .header__signout:hover {
    opacity: 0.6;
  }
.header__email {
    font-family: Inter, Arial, sans-serif;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: white;
    text-decoration: none;
  }
.profile {
    margin: 0 0 50px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media (max-width: 450px) {
    .profile {
        flex-direction: column;
        justify-content: center;
        margin-bottom: 36px;
    }
}
.profile__container {
  display: flex;
  align-items: center;
}

@media (max-width: 450px) {
  .profile__container {
    flex-direction: column;
  }
}
.profile__wrap {
    display: flex;
    align-items: center;
}
.profile__info {
    display: flex;
    flex-direction: column;
}

@media (max-width: 450px) {
    .profile__info {
