        font-weight: 500;
        max-width: 189px;
    }
}

@media (max-width: 450px) {
    .profile__name {
        -webkit-transform: translateX(18px);
                transform: translateX(18px);
    }
}
.profile__description {
    font-weight: 400;
    color: #FFFFFF;
    font-size: 18px;
    line-height: 22px;
    margin: 16px 0 0 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 336px;
}

@media (max-width: 618px) {
    .profile__description {
        font-size: 14px;
