}

@media (max-width: 450px) {
    .profile__avatar {
        margin: 0 0 26px 0;
    }
}
.profile__edit-btn {
    background-image: url(/static/media/Edit_Button.9c261a66.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px;
    background-color: transparent;
    border: 1px solid white;
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.profile__edit-btn:hover {
    opacity: 60%;
}

@media (max-width: 450px) {
    .profile__edit-btn {
        -webkit-transform: translateX(18px);
                transform: translateX(18px);
        width: 18px;
        height: 18px;
    }
}
.profile__add-btn {
    padding: 0;
    margin: 0;
    width: 150px;
    height: 50px;
    background-color: #000000;
    cursor: pointer;
    background-image: url(/static/media/Add_Button.b2fce4ac.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px;
    border: 2px solid #ffff;
    border-radius: 2px;
    justify-self: end;
