}

.profile__add-btn:hover {
    opacity: 60%;
}


@media (max-width: 450px) {
    .profile__add-btn {
        width: 282px;
        background-size: 16px;
    }
}
.profile__avatar-btn {
    background-image: url(/static/media/edit_avatar_btn.d476a8b8.svg);
    background-size: 26px;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    opacity: 0;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin: 0;
    padding: 0;
    border: 0;
  }
  
  .profile__avatar-btn:hover {
    opacity: 0.8;
    cursor: pointer;
    background-color: black;
  }
.elements {
    margin: 0 0 67px;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    grid-template-rows: auto;
    justify-content: center;
    grid-column-gap: 17px;
    -webkit-column-gap: 17px;
            column-gap: 17px;
    grid-row-gap: 20px;
    row-gap: 20px;
}

@media (max-width: 890px) {
    .elements {
        justify-items: center;
    }   
}

@media (max-width: 450px) {
    .elements {
        margin-bottom: 48px;
    }
}
.element {
    background-color: white;
    border-radius: 10px;
    max-width: 282px;
    position: relative;
}
.element__description {
    margin: 25px 15px 30px 21px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.element__name {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    margin: 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 205px;
}
.element__image {
    margin: 0;
    padding: 0;
    width: 282px;
    height: 282px;
    aspect-ratio: 1/1;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: block;
}
.element__btn-like {
    background-image: url(/static/media/like.68fe0213.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: transparent;
    border: none;
    width: 22px;
    height: 19px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    opacity: 1;
    transition: opacity 0.3s ease-out;
}

.element__btn-like:hover {
    opacity: 50%;
}
.element__btn-like_active {
    background-image: url(/static/media/like_active.302bdf76.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    width: 22px;
    height: 19px;
    cursor: pointer;
}
.element__btn-del {
    background-image: url(/static/media/Trash.4220563a.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: transparent;
    border: none;
    width: 22px;
    height: 19px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 20px;
    right: 20px;
}
.element__btn-del_none {
    display: none;
  }
.element__likes{
    min-width: 22px;
    min-height: 35px;
    display: flex;
    flex-direction: column;
  }
.element__like_counter {
    color: black;
    font-family: "Inter", Arial, sans-serif;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    margin: 0;
  }
.page {
    max-width: 880px;
    min-width: 320px;
    margin: 0 auto;
    padding-top: 45px;
    box-sizing: border-box;
    min-height: 100vh;
}
.popup {
    position: fixed;
    display: flex;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
    padding: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease-in-out;
}
.popup_showimg {
   background-color: rgba(0, 0, 0, 0.9);
}
.popup_opened {
    visibility: visible;
    opacity: 1;
}
.popup__container {
    max-width: 430px;
    background-color: white;
    border-radius: 10px;
    margin: 0;
    display: flex;
    flex-direction: column;
    position: relative;
    box-shadow: 0px 0px 25px 0px #00000026;
}

@media (max-width: 560px) {
    .popup__container {
        max-width: 282px;
    }
}
